<template>
  <div id="container" v-show="isShown"> 
      <audio id="audioNewCoin" loop="loop">

      <source src="../Content/sound/newcoin.wav" type="audio/wav"> </audio>

      <audio id="audioTopmost">

      <source src="../Content/sound/鼓掌声1.wav" type="audio/wav">

    </audio>

    <div v-show="isTopmost" class='my_pop_win' id="FormNewCoinTopmost_New" @click="newCoinClick">

      <div></div>

      <div>

        <img src="../Content/res/timgKUNDSTL1.gif">

      </div>
    </div>



    <div v-show="isTopmost==false" class='my_pop_win' id="formNewCoin_New" @click="newCoinClick">

      <div></div>

      <div>

        <img src="../Content/res/Coins-88531.gif">

      </div>

    </div>



  </div>
</template>

<script>
  export default {

    props: {

      isShown: {

        type: Boolean,

        default: false

      },

      isTopmost: {

        type: Boolean

      }

    },



    data: function () {

      return {

        my_isShown: false

      }

    },

    watch: {

      isShown: function (value) {

        if (value) {

          this.my_isShown = value;

        }

      },

      my_isShown: function (value) {

        if (value) {

          var dlg = this.isTopmost ? $('#FormNewCoinTopmost_New') : $('#formNewCoin_New');

          this.popUpMe(dlg);

          if (this.isTopmost) {

            $('#audioTopmost')[0].play();

          } else {

            $('#audioNewCoin')[0].play();

          }

        } else {



        }

      }

    },





    name: 'EndSplash',

    mounted: function () {

      // this.my_isShown=isShown;

      // if(this.isTopmost)

      //   alert("isTopmost");

      // else

      //   alert("not isTopmost");

    },

    beforeDestroy: function () {

      console.log("*** beforeDestroy ***");

    },

    methods: {

      newCoinClick: function () {

        this.my_isShown = false;



        $("#container").fadeOut(1000);



        if (this.isTopmost) {

          $('#audioTopmost')[0].pause();

        } else {

          $('#audioNewCoin')[0].pause();

        }



        this.$emit('pop_closed');

        console.log('test done');

      },



      popUpMe: function (me) {

        //me.fadeIn(1000);

        me.show();



        var bk = me.find("div:first-child")

        bk.css("opacity", 100);

        this.flashBackground(bk)



        //me.fadeIn(1000);

        var img = me.find("img");

        img.width(0);

        img.height(0);

        img.animate({

          width: $(window).width() / 2,

          height: $(window).height() / 2

        }, 2000);

      },



      flashBackground: function (bk) {

        if (!this.my_isShown) {

          console.log('stop');

          return;

        }



        console.log('flashBackground-->');

        console.log(bk.css('opacity'));



        bk.animate({

          opacity: 50

        }, 3000).animate({

          opacity: 0

        }, 3000);



        setTimeout(o => {

          this.flashBackground(bk)

        }, 6000);

      },

    }

  }

</script>

<style scoped>
  /* ---------------------------------------------------> */



  #container,

  .my_pop_win,

  .my_pop_win div {

    z-index: 999;

  }



  .my_pop_win div:first-child {

    background: yellow;

    filter: alpha(opacity=50);

    /* IE的透明度 */

    opacity: 0.5;

    /* 透明度 */

    position: absolute;

    top: 0px;

    left: 0px;

    width: 100%;

    height: 100%;

    z-index: 100;

    /* 此处的图层要大于页面 */

  }



  .my_pop_win div:last-child {

    /* .flyin{ */

    position: absolute;

    height: 50%;

    width: 50%;

    left: 25%;

    top: 25%;

    z-index: 101;

  }



  .my_pop_win img {

    width: 100%;

    height: 100%;

  }





  /* <--------------------------------------------------- */

</style>



