<template>
    <div id="container">
        <div id="up">
            <div id="MyCoinImageList" v-if="bank!=null">
                <div v-for="n in bank.CoinCntLeft" class='DivCoin' @click="onClickCoin">
                    <img class='ImgCoin' src='../Content/res/Coins-88531.gif' />
                   </div>
            </div>   
        </div>
    
        <Products :bank="bank" :showme="product_isShown" @pop_closed="onPopClose" @event_spent="onEventSpent"> </Products>
    
        <div id="down">
            <!-- <div class="caption bottom">购物车</div> -->   
            <div v-if="bank!=null" id="cart" data-options="region:'center',title:'购物车'" style="padding:5px;background:#eee;"> 
                <div class='DivProductTodayBuy' v-for="p in bank.ProductBuyToday">
                    <a href="#" :title='p.Name' class='easyui-tooltip'>  
                        <img class='ImgCoin easyui-tooltip' :src="getUri(p.Url)" width="291" height="290" />
                    </a> 
                </div>
            </div>   
        </div>  
    </div>
</template>
<script>
    import Products from './Products'
    
    export default {
    
    
    
        components: {
    
            Products
    
        },
    
        data: function() {
    
            return {


                bank: null,
    
                product_isShown: false,
                urlHeader : "/api/"
            }
    
        },
    
        watch:{
            bank:function(data){
                this.$store.commit('setFortune', data);
            }
        },
    
        mounted: function() {
    
            this.loadData();
    
        },
    
        methods: {
    
            loadData() {
                
                var url = this.urlHeader + "bank/stat";
    
                $.ajax({
                    url: url,    
                    success: (data) => {    
                        this.bank = data;    
                    }
                });
    
            },
    
            onClickCoin() {
    
                this.product_isShown = true;
    
            },
    
            onPopClose() {
    
                this.product_isShown = false;
    
            },
    
            onEventSpent() {
    
                this.loadData();
    
            },
    
            getUri: function(uri) {
                //todo
                var r = 'http://10.0.0.2//' + uri;
    
                return r;
    
            },
    
    
    
        }
    
    }
</script>



<style scoped>
    #container {
    
        position: absolute;
    
        top: 60px;
    
        bottom: 0;
    
        width: 100%;
    
        background-color: black;
    
    }
    
    
    
    #container,
    
    #container * {
    
        margin: 0;
    
        padding: 0;
    
    }
    
    
    
    #MyCoinImageList {
    
        text-align: left;
    
        width: 100%;
    
        height: 100%;
    
        overflow-y: scroll;
    
        overflow-x: hidden;
    
        background-color: navajowhite;
    
    }
    
    
    
    .caption {
    
        height: 8%;
    
        display: block;
    
        font-size: 16px;
    
        line-height: 16px;
    
        background-color: silver;
    
        width: 100%;
    
        text-align: left;
    
        font-weight: bold;
    
    }
    
    
    
    #up {
    
        height: 50%;
    
        width: 100%;
    
    }
    
    
    
    #down {
    
        width: 100%;
    
        height: 50%;
    
        background-color: lightgrey;
    
    }
    
    
    
    .DivCoin {
    
        display: inline-block;
    
        width: 100px;
    
        height: 25%;
    
    }
    
    
    
    .ImgCoin {
    
        width: auto;
    
        height: 100%;
    
    }
    
    
    
    .DivProductTodayBuy {
    
        width: 100px;
    
        height: 100px;
    
        float: left;
    
        border: 2px solid silver;
    
    }
    
    
    
    .DivProduct {
    
        width: 200px;
    
        height: 200px;
    
        float: left;
    
        border: 2px solid silver;
    
    }
    
    
    
    .DivCoinUserDef {
    
        width: 49%;
    
        height: 100%;
    
        float: left;
    
        border: 2px solid yellow;
    
    }
    
    
    
    .DivCoinUserDefForm * {
    
        font-size: 30px;
    
        /*border:1px solid brown;*/
    
    }
    
    
    
    .DivCoinUserDefForm {
    
        width: 45%;
    
        height: 100%;
    
        padding: 10px;
    
        /* float:left;  */
    
        border: 1px solid blue;
    
        background-color: lightgreen;
    
    }
    
    
    
    form {
    
        width: 90%;
    
        margin-left: 5%;
    
        margin-right: 5%;
    
        margin-top: 50px;
    
    }
    
    
    
    form>* {
    
        margin-bottom: 20px;
    
    }
    
    
    
    #btn {
    
        width: 100%;
    
        height: 80px;
    
    }
</style>


