<template>
  <!-- <div v-if="testdata!=null" id="header" class="easyui-panel" data-options="fit:true" style=" margin: 0;overflow: hidden"> -->

  <div id="header" class="easyui-panel" data-options="fit:true" style=" margin: 0;overflow: hidden">

    <router-link :to="'/'">
      <div id="progress" style="float:left; width:35%">

        <img id="imgpass" src="../Content/res/flower.gif" />

        <div id="LblCountPass" class="image_note" style="color:lightgreen">
          {{testdata==null?"":testdata. PassCount}}
        </div>

        <img id="imgfail" src="../Content/res/animated-beetle-and-bug-image-0003.gif" />
        <div id="LblCountFail" class="image_note" style="color:red">
          {{testdata==null?"":testdata.FailCount}}
        </div>

        <img src="../Content/res/flowers.gif" />
        <div id="LblCountMark" class="image_note" style="color:blue">
          {{testdata==null?"":testdata.CentCount}}
        </div>
      </div>

    </router-link>

    <router-link :to="'/Fortune/'">
      <div id="money">
        <img src="../Content/res/CoinTheRun.png" />
        <div id="lblCoinTheRun" class="image_note">
            {{fortune.AllCoinsCount}}
        </div>

        <img src="../Content/res/Coin.png" />
        <div id="lblCoin" class="image_note">
           {{fortune.CoinCntToday}}
        </div>

        <img src="../Content/res/AllCoins.png" />
        <div id="lblAllCoins" class="image_note">
          <!-- {{testdata==null?"": testdata.lblAllCoins}} -->
          {{fortune.CoinCntLeft}}
        </div>
      </div>
    </router-link>

    <div id="clock" style="float: right; height:48px;">
      <span id="TimeTopmost" class="image_note" style="color:white">
        {{testdata==null?"":testdata.TopmostSeconds}}
      </span>

      <span id="labelTime" class="image_note" style="color:blue">
        {{testdata==null?"":labelTime}}
      </span>
    </div>

  </div>
</template>

<script>
  export default {



    name: "board",

    props: {

      // labelTime:""

    },

    data: function () {

      return {
        labelTime: "",
        bank: null,

        urlHeader : "/api/"
      };

    },
    watch: {
      labelTime: function (val) {
        this.$store.commit('setLabelTime', val);
      },
    },
    mounted: function () {
      console.log('mounted');
      this.updateTimeTimerly();

      this.loadData();
    },

    computed: {
      startTime: function () {

        return this.$store.getters.startTime;

      },      

      testdata: function () {

        return this.$store.getters.testdata;

      },
      allCoinsCount: function () {

        return this.$store.state.AllCoinsCount;
    
      },

      
      fortune: function () {
        return this.$store.state.Fortune;
    
      },

    },

    methods: {
        loadData() {
            
            var url = this.urlHeader + "bank/stat";

            $.ajax({
                url: url,    
                success: (data) => {    
                    this.$store.commit('setFortune', data);                    
                }
            });

        },

      updateTimeTimerly: function () {
        //console.log("updatelabelTime");
        setInterval(() => {
          this.updatelabelTime(); //ajax
        }, 1000);
      },

      updatelabelTime: function () {
        var timestamp = Date.parse( this.startTime);
        // var now =new Date();
        var nowstamp =(new Date()).getTime();
        var sec = (new Date().getTime()- timestamp) / 1000;
        var mm = Math.floor(sec / 60);
        var ss = Math.floor(sec - mm * 60);
        if (ss < 10)
          ss = "0" + ss;

        this.labelTime = mm + ":" + ss;
      },

    }

  }

</script>

<style scoped>
  #header {

    background-color: gray;

    border: 1px solid blue;

    /*margin:0;*/

    height: 60px;

  }



  #header img {

    height: 40px;

    margin-top: 5px;

    padding: 0;

    /*border:1px solid red;*/

  }



  #header div {

    margin-top: 5px;

    padding: 0;

    /*border:1px solid black;*/

  }



  span {

    font-size: 40px;

    margin-top: 10px;

  }



  .image_note {

    display: inline-block;

    font-size: 30px;

    vertical-align: top;

    /*margin-top:10px;*/

    margin-right: 30px;

    margin-left: 5px;

  }



  #money {

    float: left;

    width: 35%;

    color: gold;

  }

</style>



