<template>
  <div id="products" v-show="my_isShown">
    <Modal v-model="dlgConfirmPara.visible" title="消费确认" @on-ok="ok" @on-cancel="cancel">
      <p>请确认：</p>
      <p>使用{{dlgConfirmPara.price}}个金币 </p>
      <p>{{dlgConfirmPara.name}} </p>
    </Modal>

    <div id="close_button" @click='onClose'>
      X
    </div>
    <div id="content">
      <div id="tabsGoods" v-if="bank!=null">
        <div v-for="price in bank.PriceList" class="price_area">
          <div class="caption"> {{price}}个金币</div>
          <div class="price_imglist">
            <div class='DivProduct' v-for="p in getProductByPrice(price)">
              <img class='ImgCoin' :src="getUri(p.Url)" @click="selectProduct(p.Price, p.Name)" />

            </div>
          </div>
        </div>
      </div>

      <div id="price_userDef">
        <div class="caption"> N个金币</div>
        <div>
          <div class='DivCoinUserDef'>

            <img class='ImgCoin' src='../Content/reward/X-自定义.gif' />

          </div>
          <div class='DivCoinUserDefForm'>
            <form>
              <div style="width:100%;">
                <span>金币数:</span>
                <span>
                  <select id="usedefPrice" v-model="userDefCoinNumber">
                    <option v-for="i in 20">{{i}}</option>
                  </select>
                </span>

                <span>用途:</span>
                <input id="usage" v-model="userDefCoinNote" />
                <input id="btn" type="submit" value="提交" @click.stop.prevent="onSubmitUserDef" />
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>

  </div>

</template>

<script>
  export default {
    props: ["showme", "bank"],

    data: function () {
      return {
        userDefCoinNumber: 1,
        userDefCoinNote: "",
        my_isShown: false,

        dlgConfirmPara: {
          visible: false,
        },

        urlHeader : "/api/"
      }
    },

    watch: {
      showme: function (value) {
        if (value) {
          this.my_isShown = value;
        }
      }
    },
    computed: {


    },
    mounted: function () {

    },
    methods: {
      getUri: function (uri) {
        //todo
        var r = 'http://10.0.0.2/' + uri;
        return r;
      },

      getProductByPrice: function (i) {
        var p = this.bank.Products.filter(x => x.Price == i);
        return p;
      },

      selectProduct(price, name) {
        //todo confirm it
        this.dlgConfirmPara.visible = true;
        this.dlgConfirmPara.price = price;
        this.dlgConfirmPara.name = name;
        return;
      },

      onSubmitUserDef() {
        // alert(this.userDefCoinNumber+this.userDefCoinNote);
        this.dlgConfirmPara.visible = true;
        this.dlgConfirmPara.price = this.userDefCoinNumber;
        this.dlgConfirmPara.name = this.userDefCoinNote;
      },

      ok() {

        this.SubmitCostData(this.dlgConfirmPara.price, this.dlgConfirmPara.name);
        this.dlgConfirmPara.visible = false;
      },
      cancel() {
        // this.$Message.info('Clicked cancel');
        this.dlgConfirmPara.visible = false;
      },

      SubmitCostData(price, name) {
        var mydata = {
          price: price,
          productName: name,
        };
        var url = this.urlHeader+"bank/buy";
        $.ajax({
          url: url,
          data: mydata,
          dataformat: 'json',
          success: (data) => {
            //  alert('OK1:'+url);
            console.log(data);
            if (data!= "ok") {
              // alert(data.ErrorMsg);
              this.$Message.warning(data);
            } 
            else {
              // alert('no errormsg');
              this.$Message.info(`已消费${this.dlgConfirmPara.price }个金币${this.dlgConfirmPara.name }`);
              // alert('OK');
              this.$emit('event_spent');
            }
          },
          error : function() {
              alert('失败:'+url);
          }
        });
      },



      onClose() {
        this.my_isShown = false;
        this.$emit('pop_closed');
      }

    }
  }

</script>

<style scoped>
  #products {
    position: absolute;
    top: 10%;
    bottom: 20%;
    left: 10%;
    right: 10%;
    background-color: lightcyan;
    border: 2px solid greenyellow;

  }

  #content {
    position: absolute;
    top: 7%;

    width: 100%;
    height: 93%;
    overflow-y: scroll;
    overflow-x: hidden;
  }

  .price_area {
    float: left;
    width: 45%;
    margin: 1% 2%;
    border: 2px solid green;
    border-radius: 10px;
  }

  .caption {
    border-radius: 8px;
    line-height: 25px;
    background-color: gold;
    border-bottom: 1px solid black;
    /* border-right: 2px solid black; */
  }

  .price_imglist {
    display: block;
    text-align: left;
    /* height:500px; */
    width: 100%;

  }

  .DivProduct {
    display: inline-block;
    width: 100px;
    height: 100px;
    margin: 10px;
    border: 1px solid silver;
  }

  .ImgCoin {
    width: 100%;
    height: 100%;
  }


  #tabsGoods {
    display: block;
    width: 100%;
    height: 100%;
    overflow: hidden;
    /* background-color: crimson; */
  }

  #price_userDef {
    margin: 1% 2%;
    display: block;
    width: 96%;
    height: 160px;

    /* margin: 1% 2%; */
    border: 2px solid green;
    border-radius: 5px;
  }

  #price_userDef span {
    margin-left: 20px;

  }

  .DivCoinUserDef {
    margin: 10px;
    width: 100px;
    height: 100px;
    float: left;
    border: 2px solid yellow;
  }

  .DivCoinUserDefForm * {
    font-size: 30px;

  }

  .DivCoinUserDefForm {
    float: left;
    text-align: left;
    width: 80%;
    height: 100%;
    /* padding:10px; */

    /* border:1px solid blue;   
            background-color:lightgreen; */
  }

  #btn {
    /* float:right; */
    margin-left: 40px;
    width: 300px;
    height: 40px;
  }


  form {
    width: 90%;
    margin-left: 5%;
    margin-right: 5%;
    margin-top: 50px;
  }

  form>* {
    margin-bottom: 20px;
  }

  #close_button {
    margin: 0;
    padding: 0;

    position: absolute;
    top: 0;
    right: 0;
    height: 30px;
    width: 50px;
    background-color: black;
    color: white;

    font-size: 20px;
    border: 2px solid silver;
    cursor: pointer;

  }

  /* #close_button button{
             margin:0;
             padding:0;
            font-size: 20px;
         } */

</style>



