<template>
  <div>
    <EndSplash 
    :isTopmost="splash_isTopmost" 
    :isShown="splash_shown" @pop_closed="onPopClosed"></EndSplash>

    <div id="cc">
      <div id="topbar">

        <div id="area-question">
          <div id="ask" class="topnumber">
            {{testdata.Questions!=undefined?testdata.Questions[curindex].Ask:''}}
          </div>
          <div id="operator" class="topnumber">
            =
          </div>

          <div class="topnumber" id="answer">
            <input id="my_answer" v-model="my_answer" @keydown="onInputKeydown" type="text" />
          </div>
        </div>
        <div id="area_submit">
          <div id="lblLast" :style="ComputeStyle4Last">
            {{lblLast}}
          </div>

          <div style="float: left; width: 40%; ">
            <input type="submit" id="submit" value="提交" @click="onSubmit">
            <button id="del" @click="onDelClick">删除</button>
          </div>
        </div>
      </div>
      <div id='layout_west'>
        <!-- <div id='console'>log is {{log}}</div> -->
        <div id="MyTaskImageList" v-if="testdata!=null && testdata.Questions!=null ">
          <div v-for="item in GetDoneQuestions">
            <div class='myicon'>
              <a href='#' :title="item.Ask+' = '+item.Answer+'; '+item.TesterAnswer">
                <img v-if="item.IsPass" id="model_imgpass" src="../Content/res/flower.gif" class="imgRec" />
                <img v-else id="model_imgfail" src="../Content/res/animated-beetle-and-bug-image-0003.gif" class="imgRec" />
              </a>
            </div>

          </div>


        </div>
      </div>
      <div id='layout_center'>
        <div id="numberpad">
          <div>
            <button @click="onNumberkeyClick">7</button>
            <button @click="onNumberkeyClick">8</button>
            <button @click="onNumberkeyClick">9</button>
          </div>
          <div>
            <button @click="onNumberkeyClick">4</button>
            <button @click="onNumberkeyClick">5</button>
            <button @click="onNumberkeyClick">6</button>
          </div>
          <div>
            <button @click="onNumberkeyClick">1</button>
            <button @click="onNumberkeyClick">2</button>
            <button @click="onNumberkeyClick">3</button>
          </div>
          <div>
            <button @click="onNumberkeyClick" id="numkey_last">0</button>
          </div>
        </div>

      </div>
    </div>


    <audio id="audioError">
      <source src="../Content/sound/error.wav" type="audio/wav">
    </audio>
    <audio id="audioPassError">
      <source src="../Content/sound/LASER.wav" type="audio/wav">
    </audio>
    <audio id="audioPassRight">
      <source src="../Content/sound/Speech On.wav" type="audio/wav">
    </audio>
  </div>
</template>

<script>
  import EndSplash from "./EndSplash";
  import Board from "./Board";

  export default {
    components: {
      EndSplash,
      Board
    },
    name: 'Tester',
    data() {
      return {
        testdata: {},

        my_answer: '',

        splash_shown: false,
        splash_isTopmost: false,

        startTime: new Date(),

        testRecs: [],
        log: 'to do...',

        // urlHeader : "http://10.0.0.2/cocovue/api/"
        urlHeader: "/api/",

        lblLast: '',

        IsAutoSubmit: true,

        curindex: 0,
        

        QuestionLast_IsPass:true
      }
    },
    mounted: function () {
      try {
        var mydata = this.$store.getters.testdata;
        if (mydata == null) {
          this.log = 'mydata==null';
          this.loadTestData();

          this.log += 'after loadTestData';
        } else {
          this.log += 'mydata!=null';

          this.testdata = mydata;
        }

      } catch (error) {
        this.log = 'mydata==null';
      }

    },

    watch: {
      my_answer: function (val) {
        console.log(val);
        this.tryAutoSubmit();
      },
      testdata: function (val) {
        this.$store.commit('setTestData', val);
      },



      startTime: function (val) {
        if (val != null)
          this.$store.commit('setStartTime', val);
      },
    },

    computed: {
      GetDoneQuestions(){
        var r = this.testdata.Questions.filter(item=>item.IsPass!=null);
        return r;
      },
        

      ComputeStyle4Last: function () {
        if (this.QuestionLast_IsPass==true)
          return {
            'background-color': 'green'
          };
        else
          return {
            'background-color': 'red'
          };
      }
    },

    methods: {
      onNumberkeyClick: function () {
        var n = $(event.target).text();
        this.my_answer += n;
      },

      onInputKeydown: function () {
        var code = event.keyCode;
        if (code == 13) {
          this.onSubmit();
          return true;
        }

        //del ok
        if (code == 8) {
          this.IsAutoSubmit = false;
          return true;
        }

        if (!((code >= 48 && code <= 57) || (code >= 96 && code <= 105))) {
          if (code == 123) {
            console.log('F12 press');
            return;
          }

          $('#audioError')[0].play();
          return false;
        }
      },

      onDelClick: function () {
        this.my_answer = "";
        this.IsAutoSubmit = false;
      },


      onSubmit: function () {
        if (this.my_answer.length == 0) {
          $('#audioError')[0].play();
          return;
        }

        var questionNow = this.testdata.Questions[this.curindex]
        var IsPass= questionNow.Answer == this.my_answer;
        
        


        questionNow.IsPass =IsPass;

        if (IsPass){           
          this.testdata.CentCount++;
          this.testdata.PassCount++;
        }      
        else{
          this.testdata.CentCount--;
          this.testdata.PassCount++;
        }
          
        
        this.QuestionLast_IsPass =IsPass;
        this.lblLast = questionNow.Ask + " = " + this.my_answer;        
        if (!IsPass)
          this.lblLast += " (" + this.my_answer + ")";
                  

        if ( this.testdata.CentCount< this.testdata.MyRound && this.curindex<=this.testdata.Questions.length-2) {
          this.curindex++;            


          if (IsPass) {
            $('#audioPassRight')[0].play();
          } else {
            $('#audioPassError')[0].play();
          }

      

         //答题区清空
          this.my_answer = "";
          $("#my_answer").focus();

          this.IsAutoSubmit = this.testdata.IsAutoSubmit;
        } 
        else {
            //在此提交考试结束
             this.callEndTest();

           
        }

        //--------------------------------------------------

      },
      callEndTest(){
        var url = this.urlHeader + "quizzesex";
        var mydata = JSON.stringify(this.testdata);
        $.ajax({
          type:"POST",
          xhrFields: {
            withCredentials: true
          },

          data: mydata,//this.testdata,
          dataType : 'json',
          contentType:"application/json",
          url: url,
          success: (data) => {
            this.splash_isTopmost = data;
            this.splash_shown = true;
          }
          });
      },

      tryAutoSubmit() {
        if (!this.IsAutoSubmit)
          return;

        var result = this.my_answer;

        if (result.length == 0) {
          return;
        }

        if (this.testdata.Questions[this.curindex].Answer == result) {
          this.onSubmit();
        }
      },



      onPopClosed: function () {
        this.splash_shown = false;

        console.log('onPopClosed');

        this.loadTestData();
      },

      loadTestData() {
        var url = this.urlHeader + "quizzesex/0";
        $.ajax({
          xhrFields: {
            withCredentials: true
          },
          url: url,
          success: (data) => {
            this.testdata = data;


            this.curindex = 0;
            
            this.lblLast ="";
            this.QuestionLast_IsPass = true;

            this.IsAutoSubmit = data.IsAutoSubmit;
            this.startTime = new Date();
            this.resetUI();
          },
          error: function (jqXHR, textStatus, errorThrown) {
            // alert('失败:jsonp- '+url+'; '+textStatus+'; '+errorThrown);
            // alert(jqXHR.status);
            // this.log += textStatus;
            // this.log += errorThrown;
          }
        });

        var urlFortune = this.urlHeader + "bank/stat";
        $.ajax({
          url: urlFortune,
          success: (data) => {
            this.$store.commit('setFortune', data);
          }
        });


      },

      resetUI: function () {
        this.lblLast = "";
        this.my_answer = "";

        $("#my_answer").focus();
        $(".my_pop_win").hide();
      },


    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style>
  .myicon {
    width: 20%;
    height: 20%;
    float: left;
  }

  .imgRec {
    width: 100%;
    height: 100%;
  }

</style>

<style scoped>
  .image_note {
    display: inline-block;
    font-size: 30px;
    vertical-align: top;
    /*margin-top:10px;*/
    margin-right: 30px;
    margin-left: 5px;
  }


  /*question bar---------------------------------------------------------------------------------------*/

  #topbar {
    height: 130px;
    background: #fafafa;
    margin: 0;
    overflow: hidden;
    /* border-bottom: 2px solid silver; */
  }

  #area-question {
    margin: 0;
    padding: 0;
    font-size: 20px;
    height: 50%;
    width: 100%;
    background-color: lightgrey;
    /* border-bottom: 2px solid grey; */
  }


  /*north---------------------------------------------------------------------------------------*/

  #ask {
    width: 45%;
    line-height: 100%;
    vertical-align: middle;
    text-align: right;
    font-size: 50px;
    background-color: lightyellow;
    padding-right: 5%;
  }

  #operator {
    width: 5%;
    font-size: 50px;
    text-align: center;
  }


  /* .topnumber {
    font-size: 20px;
  } */

  #answer {
    margin: 0;
    padding: 0;
    width: 45%;
    height: 100%;
    line-height: 100%;
  }

  #my_answer {
    margin: 0;
    padding: 0;
    height: 100%;
    font-size: 50px;
    border: none;
    width: 100%;
    letter-spacing: 8px;
    background-color: lightgreen;
  }

  #area_submit {
    height: 50%;
  }

  #lblLast {
    /* border: 3px solid red; */
    /* vertical-align: middle; */
    font-size: 30px;
    /* line-height: 100%; */
    padding-right: 7%;
    width: 46%;
    height: 80%;
    padding-top: 10px;
    float: left;
    text-align: right;
  }

  #submit {
    margin: 10px;
    width: 40%;
    float: left;
    font-size: 25px;
    height: 50px;
  }

  #del {
    margin: 10px;
    width: 40%;
    float: right;
    font-size: 25px;
    height: 50px;
  }


  /*west---------------------------------------------------------------------------------------*/

  #layout_west {
    margin: 0;
    padding: 0;
    position: absolute;
    top: 190px;
    bottom: 0;
    width: 50%;
  }

  #MyTaskImageList {
    width: 100%;
    height: 100%;
    /* background-color: blue; */
  }

  #ioconlist {
    width: 100%;
    height: 100%;
    background-color: lightgrey;
  }


  /*center---------------------------------------------------------------------------------------*/

  #layout_center {
    z-index: 0;
    margin: 0;
    padding: 0;
    position: absolute;
    top: 190px;
    bottom: 0;
    left: 50%;
    right: 0;
    /* background-color: yellow; */
    /* width:30%; */
    /* border: 1px solid red; */
    /* margin-left:50%; */
  }

  #numberpad {
    height: 90%;
    overflow: hidden;
    padding: 5px;
    background: white;
    padding-top: 20px;
  }

  div.topnumber {
    /*display: inline-block;*/
    float: left;
    /*border: 2px solid darkgreen;*/
    /*height: 100%;*/
    vertical-align: middle;
    line-height: 100%;
    height: 100%;
    letter-spacing: 5px;
  }


  /*DIV.numrow{*/

  #numberpad>div {
    height: 20%;
    margin-bottom: 2%;
  }


  /*button.numkey{*/

  #numberpad button {
    font-size: 25px;
    color: red;
    width: 27%;
    margin-left: 5%;
    height: 99%;
    margin-bottom: 1%;
    display: inline-block;
    border: 1px solid black;
  }

  #numkey_last {
    width: 60% !important;
  }

  .myicon {
    width: 20%;
    height: 20%;
    float: left;
  }

  .myimg {
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%;
    min-width: 100%;
    min-height: 100%;
  }

</style>



