import Vue from 'vue'
import Router from 'vue-router'
import Tester from '@/components/Tester'
import Fortune from '@/components/Fortune'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Tester',
      component: Tester
    },
    {
      path: '/Fortune',
      name: 'Fortune',
      component: Fortune
    },    
  ]
})



