import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);



const cfg = {
    state: {
        testdata: null,
        labelTime: "",
        startTime: "",
        AllCoinsCount: "",
        
        Fortune: {}
    },
    getters: {
        testdata: state => {
            // if(!state.testdata){
            //     state.testdata = JSON.parse(sessionStorage.getItem('testdata'))
            // }
            return state.testdata;
        },
        labelTime: state => {
            // if(!state.labelTime){
            //     state.labelTime = JSON.parse(sessionStorage.getItem('labelTime'))
            // }
            return state.labelTime;
        },
        startTime: state => {
            // if(!state.startTime){
            //     state.startTime = JSON.parse(sessionStorage.getItem('startTime'))
            // }
            return state.startTime;
        },
    },
    mutations: {
        //kkk 同步方法，第一个参数必须是 state
        setTestData (state, data) {
            state.testdata = data;
            // sessionStorage.setItem('testdata',JSON.stringify(data));
        },

        setLabelTime (state, data) {
            state.labelTime = data;
            // sessionStorage.setItem('labelTime',JSON.stringify(data));
        },

        
        setStartTime (state, data) {
            state.startTime = data;
            // sessionStorage.setItem('startTime',JSON.stringify(data));
        },


        setFortune (state, data) {
            state.Fortune = data;
        }
    },
}

var myStore = new Vuex.Store(cfg);

export default myStore;


